export default {
    data() {
      return {
        days: [
          { name: "Понедельник" },
          { name: "Вторник" },
          { name: "Среда" },
          { name: "Четверг" },
          { name: "Пятница" },
          { name: "Суббота" },
          { name: "Воскресенье" },
        ],
        searchStr: "",
      };
    },
    computed: {
      daysFilter() {
        return this.searchStr === ""
          ? this.days
          : this.days.filter((item) => item.name === this.searchStr);
      },
    },
  };