<template>
  <div id="app">
    <h1>{{ msg }}</h1>
    <hr />
    <h1>{{ msg | lowercase }}</h1>
    <hr />
    <h1>{{ msg | uppercase }}</h1>
    <hr />
    <h1>{{ msg | uppercase | slice(14, 21) }}</h1>
    <hr />
    <div id="row">
      <div>
        <h2>Компонент</h2>
        <app-days></app-days>
      </div>
      <div>
        <h2>Родитель</h2>
        <div id="app-days">
          <input type="text" v-model="searchStr" />
          <p v-for="days in daysFilter" v-bind:key="days.name">
            {{ days.name }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import daysMixin from "./components/listMix.js";
export default {
  data() {
    return {
      msg: "Welcome to my Vue App!",
    };
  },
  mixins: [daysMixin],
  filters: {
    lowercase(str) {
      return str.toLowerCase();
    },
    slice(str, i1, i2) {
      return str.slice(i1, i2);
    },
  },
};
</script>

